import { Badge, Box, Center, HStack, Heading, Icon, Img, Tag, Text, useColorModeValue, useToast } from "@chakra-ui/react";

import { Link } from "react-router-dom";
import { TagIcon } from "../assets/icons";
import config from "../config";
import formatDuration from "../lib/formatDuration";
import humanTime from "../lib/humanTime";

export default function VideoCard({ single_img, title, uploaded, file_code, views, canplay, length, download_url, splash_img }) {
    const color = useColorModeValue("black", "white");
    const toast = useToast({ position: "top" });

    uploaded = uploaded + ".000Z"
    const _new = new Date(uploaded) > new Date(new Date() - 1000 * 60 * 60 * 8);
    const tag = title.split(" ")[0];

    if (!download_url) {
        download_url = config.DOOD_BASE_URL + "/d/" + file_code;
    }

    return (
        <Center>
            <Box w="100vw" overflow={"hidden"} bg={useColorModeValue("white", "black.800")} border={"1px"} borderColor="black.900" borderRadius={{ md: 10 }} h={"100%"}>
                <Box h={[130, 200]} borderBottom={"1px"} borderColor="black" position={"relative"}>
                    <Link
                        to={"/video/" + file_code}
                        {...(!canplay && {
                            onClick: () => {
                                toast({
                                    title: "Video is not ready to play, you can download it instead.",
                                });
                            },
                        })}
                    >
                        <Img
                            src={single_img}
                            objectFit="cover"
                            h="full"
                            w="full"
                            alt={title}
                            loading="lazy"
                            {...(splash_img && {
                                _hover: {
                                    content: `url(${splash_img})`,
                                    cursor: "pointer",
                                },
                            })}
                        />
                    </Link>
                    <Tag fontSize={["xs", "sm"]} size={["sm", "md"]} bottom={1} right={1} position={"absolute"} backgroundColor={"rgba(0, 0, 0, 0.8)"}>
                        {formatDuration(length)}
                    </Tag>
                    <Badge as={Link} to={"/tag/" + tag} position={"absolute"} top={1} left={1}>
                        <Icon as={TagIcon} mb={-0.5} me={0.5} />
                        {tag}
                    </Badge>
                </Box>
                <Box p={[2, 4]}>
                    <Heading color={color} fontSize={["sm", "lg"]} noOfLines={2}>
                        {_new && (
                            <Tag size={"sm"} me={1} backgroundColor={"green.500"}>
                                New
                            </Tag>
                        )}
                        {title}
                    </Heading>
                    <HStack opacity={0.7}>
                        <Text as="small">{views} views</Text>
                        <Text as="small">&bull;</Text>
                        <Text as="small">{humanTime(uploaded)}</Text>
                    </HStack>
                </Box>
            </Box>
        </Center>
    );
}
