import { Box, Heading } from "@chakra-ui/react";

import Error from "../components/Error";
import Loading from "../components/Loading";
import VideoList from "../components/VideoList";
import config from "../config";
import fetcher from "../lib/fetcher";
import useSWRImmutable from "swr/immutable";
import { useSearchParams } from "react-router-dom";

function Search() {
    const [searchParams,] = useSearchParams();
    const { data, error } = useSWRImmutable(config.api.search + "?search_term=" + searchParams.get("q"), fetcher);

    window.scrollTo({
        top: 0,
    });

    if (error) return <Error status={error.status} message={error.message} />;
    if (!data) return <Loading />;

    document.title = "Search results for " + searchParams.get("q") + " - " + window.config.SITENAME;

    return (
        <Box>
            <Heading textAlign={"center"} size={"md"} my={5} fontFamily={"mono"}>
                { data.length === 0 ? "No result found" : "Search results"} for "{searchParams.get("q")}"
            </Heading>
            <VideoList files={data} />
        </Box>
    );
}

export default Search;
