import {
    Box,
    Button,
    Collapse,
    Flex,
    Heading,
    Icon,
    Input,
    InputGroup,
    InputLeftElement,
    Stack,
    useBreakpointValue,
    useColorMode,
    useColorModeValue,
    useDisclosure,
    useToast,
} from "@chakra-ui/react";
import { Link, useNavigate } from "react-router-dom";
import { MoonIcon, SearchIcon, SunIcon } from "../assets/icons";

// const NavLink = ({ children }) => (
