//             bg: useColorModeValue("gray.200", "gray.700"),
//         }}
//         href={"#"}
//     >
//         {children}
//     </Link>
// );

const SearchBox = ({ hidden, onSubmit }) => (
    <Box as="form" hidden={hidden} onSubmit={onSubmit}>
        <InputGroup>
            <InputLeftElement pointerEvents="none">
                <Icon as={SearchIcon} fontSize={18}></Icon>
            </InputLeftElement>
            <Input name="query" placeholder="Search"></Input>
        </InputGroup>
    </Box>
);

export default function Navbar() {
    const { colorMode, toggleColorMode } = useColorMode();
    const isMobile = useBreakpointValue({ base: true, md: false });
    const { isOpen, onToggle } = useDisclosure();
    const toast = useToast({ position: "top", isClosable: true });
    const navigate = useNavigate();

    const onSearch = (e) => {
        e.preventDefault();
        if (!e.target.query.value) return toast({ title: "Please enter a search query", status: "warning" });
        navigate("/search?q=" + e.target.query.value);
    };

    return (
        <Box bg={useColorModeValue("white", "black.700")} px={6}>
            <Flex h={16} alignItems={"center"} justifyContent={"space-between"}>
                <Heading as={Link} to="/" color={"whiteAlpha.800"}>
                    {window.config.SITENAME}
                </Heading>
                <Flex alignItems={"center"}>
                    <Stack direction={"row"} spacing={[2, 7]}>
                        <SearchBox hidden={isMobile} onSubmit={onSearch}></SearchBox>
                        <Icon fontSize={"2xl"} as={SearchIcon} onClick={onToggle} hidden={!isMobile}></Icon>
                        <Button onClick={toggleColorMode} borderRadius={"3xl"} hidden={!window.config.ENABLE_DUAL_THEME}>
                            {colorMode === "light" ? <MoonIcon /> : <SunIcon />}
                        </Button>
                    </Stack>
                </Flex>
            </Flex>
            <Collapse in={isOpen} animateOpacity>
                <Box pb={4}>
                    <SearchBox onSubmit={onSearch}></SearchBox>
                </Box>
            </Collapse>
        </Box>
    );
}
